import {
  checkRichtextEmpty,
  replacer,
  parseRichProperties,
} from '@/question-bank/richtext'
import {
  GRADES,
  SUBJECT_MIXED,
} from '@/assets/js/consts/common'
import {
  HC_STATES_NAME_MAP,
  HC_STATE_EDITING,
  HC_STATE_PUBLISHED,
  HC_STATE_SUSPENDED,
  HC_STATE_DELETED,
  COURSE_TYPE_INTRAMURAL,
  COURSE_TYPE_QINGBEI,
  DEVICE_TYPE_ALL,
  COURSE_TYPE_MAP,
  DEVICE_TYPE_MAP,
} from '@/assets/js/consts/holiday-course'

export class HCLessonMaterial {
  constructor(sourceData = {}) {
    this.id = sourceData.id || null
    this.lessonId = sourceData.lessonId
    this.seqId = sourceData.seqId || 1
    this.title = sourceData.title || null
    this.classKind = sourceData.classKind || null

    this.fileType = sourceData.fileType
    this.filePath = sourceData.filePath || null
  }
}


export class HCLesson {
  constructor(sourceData = {}) {
    this.id = sourceData.id || null
    this.courseId = sourceData.courseId
    this.seqId = sourceData.seqId || 1
    this.seconds = sourceData.seconds || 0
    this.title = sourceData.title || null
    this.teacherName = sourceData.teacherName || null
    this.subjectId = sourceData.subjectId

    this.videoPath = sourceData.videoPath || null
    this.startTime = sourceData.startTime || null

    this.materials = []
    if (sourceData.materials) {
      for (const materialData of sourceData.materials) {
        this.materials.push(new HCLessonMaterial(materialData))
      }
    }
  }

  validate(course) {
    if (course.id !== this.courseId) {
      return [false, '']
    }
    if (this.title.length > 100 || !this.title) {
      return [false, '100']
    }
    if (this.teacherName > 30 || !this.teacherName) {
      return [false, '30']
    }
    if (!this.startTime) {
      return [false, '']
    }
    if (this.startTime < course.startDate || this.startTime > course.endTime) {
      return [false, '']
    }
    if (course.subjectId !== SUBJECT_MIXED && course.subjectId !== this.subjectId) {
      return [false, '']
    }
    return [true, '']
  }

  get hasExpired() {
    return new Date(this.startTime) < new Date()
  }
}


export class HolidayCourse {
  constructor(sourceData = {}) {
    this.id = sourceData.id || null
    this.title = sourceData.title || ''
    this.mainTeacherName = sourceData.mainTeacherName || ''
    this.subjectId = sourceData.subjectId || 1
    this.gradeId = sourceData.gradeId || null
    this.startDate = sourceData.startDate || null
    this.endDate = sourceData.endDate || null
    this.startTime = sourceData.startTime || '10:00'
    this.state = sourceData.state || null
    this.courseType = sourceData.courseType || COURSE_TYPE_INTRAMURAL
    this.deviceType = sourceData.deviceType || DEVICE_TYPE_ALL

    this.nativeStudentOnly = sourceData.nativeStudentOnly || false
    this.descriptionRich = sourceData.descriptionRich || {}

    this.demoVideoPath = sourceData.demoVideoPath || null
    this.demoDescriptionRich = sourceData.demoDescriptionRich || {}

    this.hasPaperMaterial = sourceData.hasPaperMaterial || false
    this.paperMaterialTitle = sourceData.paperMaterialTitle || ''

    this.playbackMethod = sourceData.playbackMethod || 2
    this.playbackRelativeDays = sourceData.playbackRelativeDays || 1
    this.playbackAbsoluteDate = sourceData.playbackAbsoluteDate || null

    this.originalPrice = sourceData.originalPrice || 0
    if (!this.nativeStudentOnly) {
      this.applyCondition = { targetKind: 0, targetIds: [] }
    } else {
      this.applyCondition = sourceData.applyCondition
    }

    this.materialsFilled = sourceData.materialsFilled || false

    this.school = sourceData.school || {} // 
    this.lessonCount = sourceData.lessonCount || 0

    parseRichProperties(this)
  }

  stringify() {
    return JSON.stringify(this, replacer)
  }

  get courseTypeLabel() {
    return COURSE_TYPE_MAP[this.courseType]
  }

  get isQingbeiPeiyou() {
    return this.courseType === COURSE_TYPE_QINGBEI
  }

  get deviceTypeLabel() {
    return DEVICE_TYPE_MAP[this.deviceType]
  }

  get applyConditionDisplay() {
    if (!this.nativeStudentOnly) return '()'

    const kind = this.applyCondition.targetKind
    const ids = this.applyCondition.targetIds

    if (kind === 1) return ''
    if (kind === 2) {
      const gLabels = ids.map((id) => GRADES[id])
      const label = gLabels.join('')
      return ` ${label} `
    }
    if (kind === 3) {
      const cLabels = ids.map((id) => {
        for (const c of this.school.classes) {
          if (c.id === id) return c.name
        }
        return null
      })
      const label = cLabels.join('')
      return ` ${label} `
    }
  }

  get originalPriceDisplay() {
    const fixed = this.originalPrice.toFixed(2)
    if (this.originalPrice) return `${fixed} `
    return `${fixed} `
  }

  get endTime() {
    // 
    return `${this.endDate} 23:59:59`
  }

  get isMixed() {
    return this.subjectId === SUBJECT_MIXED
  }

  get isStateEditing() {
    return this.state === HC_STATE_EDITING
  }

  get isStatePublished() {
    return this.state === HC_STATE_PUBLISHED
  }

  get isStateSuspended() {
    return this.state === HC_STATE_SUSPENDED
  }

  get isStateDeleted() {
    return this.state === HC_STATE_DELETED
  }

  get playbackDisplay() {
    if (this.playbackMethod === 1) {
      return `${this.playbackAbsoluteDate} `
    } else if (this.playbackMethod === 2) {
      return ` ${this.playbackRelativeDays} `
    }
  }

  get stateDisplay() {
    return HC_STATES_NAME_MAP[this.state]
  }

  get paperMaterialDisplay() {
    if (this.hasPaperMaterial) return `${this.paperMaterialTitle}`
    return ''
  }

  validate() {
    if (this.title.length > 100 || !this.title) {
      return [false, '100']
    }
    if (this.mainTeacherName > 30 || !this.mainTeacherName) {
      return [false, '30']
    }
    if (!this.gradeId) {
      return [false, '']
    }
    if (this.hasPaperMaterial) {
      if (this.paperMaterialTitle > 30 || !this.paperMaterialTitle) {
        return [false, '100']
      }
    }
    if (!this.startDate || !this.endDate) {
      return [false, '']
    }
    if (this.nativeStudentOnly) {
      const targetKind = this.applyCondition.targetKind
      const targetIds = this.applyCondition.targetIds || []

      if (![1, 2, 3].includes(targetKind) || targetIds.length === 0) {
        return [false, '']
      }
    }
    if (![1, 2].includes(this.playbackMethod)) return [false, '']
    if (this.playbackMethod === 1 && (!this.playbackAbsoluteDate  )) return [false, '']
    if (this.playbackMethod === 2 && this.playbackRelativeDays < 1) {
      return [false, '']
    }
    if (checkRichtextEmpty(this.descriptionRich)) return [false, '']
    if (this.originalPrice === null) return [false, '']

    return [true, '']
  }
}




