<template>
  <div>
    <loader v-if="loading"></loader>
    <div v-else>
      <div class="content-empty" v-if="!students.length">
        <img :src="require('assets/img/content-empty.png')">
        <p>...</p>
      </div>
      <table class="pad ui small table" v-else>
        <thead>
          <tr>
            <th>ID</th>
            <th></th>
            <th v-if="showClasses"></th>
            <th v-if="showClasses"><span class="text-muted"></span></th>
            <th></th>
            <th></th>
            <th class="right aligned"></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="s of students" :key="'student-row-' + s.id" class="content-row">
            <td>{{s.id}}</td>
            <td class="row-center-title"> {{s.name}} </td>
            <td v-if="showClasses">
              <template v-for="c in s.classes">
                <div v-if="c.isNormal">
                  <span @click="openClassInNewTab(c)" class="link link-primary">{{c.name}}</span>
                  <span class="text-muted">{{GRADES[c.gradeId]}} - {{c.year}} </span>
                </div>
              </template>
            </td>
            <td v-if="showClasses">
              <template v-for="c in s.classes">
                <div v-if="c.isArchived">
                  <span @click="openClassInNewTab(c)"class="link link-default">{{c.name}}</span>
                  <span class="text-muted">{{GRADES[c.gradeId]}} - {{c.year}} </span>
                </div>
              </template>
            </td>
            <td>{{s.number}}</td>
            <td>{{s.status ? '' : ''}}</td>
            <td class="right aligned" v-if="klass && klass.isArchived">
              <span class="text-muted"></span>
            </td>
            <td class="right aligned" v-else>
              <span
                v-if="isStatusNormal(s) && teacher.isManager"
                class="link link-danger" @click.stop="sToBan=s"></span>
              <span
                v-if="!isStatusNormal(s) && teacher.isManager"
                class="link link-primary" @click.stop="sToRecover=s"></span>
              <span
                v-if="klass"
                class="link link-primary"
                @click.stop="sToResetPassword=s"></span>
              <span class="link link-primary" @click.stop="openEditStudentModal(s)"></span>
              <span v-if="teacher.isManager && klass" class="link link-primary" @click.stop="openTransferModal(s)"></span>
              <span v-if="teacher.isManager && klass" class="link link-primary" @click.stop="sToExitClass=s"></span>
            </td>
          </tr>
        </tbody>
      </table>
      <pagination
        class="align-left compact"
        :totalPage="totalPage"
        :curPage="curPage"
        @update:selected="(page) => {$emit('update:selected', page)}"
        v-show="students.length"></pagination>
    </div>
    <page-modal
      v-if="sToResetPassword"
      @close="sToResetPassword=null"
      class="modal-small modal-padded">
      <div slot="content">
        <p class="ui small header"></p>
        <p> <b>{{sToResetPassword.name}}</b> </p>
        <button class="ui tiny blue button right floated" @click="postResetPassword($event)"></button>
        <button class="ui tiny button right floated" @click="sToResetPassword=null"></button>
      </div>
    </page-modal>
    <page-modal
      v-if="sToEdit"
      @close="sToEdit=null"
      class="modal-small modal-padded">
      <div slot="content">
        <p class="ui small header"> {{sToEdit.name}} </p>
        <div class="ui small form">
          <div class="field required">
            <label></label>
            <input type="text" v-model="snameToEdit" placeholder="">
          </div>
          <div class="field">
            <label></label>
            <input type="text" v-model="snumberToEdit" placeholder="20">
          </div>
          <div class="field">
            <button class="ui tiny blue button" @click="postEditStudent($event)"></button>
            <button class="ui tiny button" @click="sToEdit=null"></button>
          </div>
        </div>
      </div>
    </page-modal>
    <page-modal
      v-if="sToExitClass && klass"
      @close="sToExitClass=null"
      class="modal-padded modal-small">
      <div slot="content">
        <p class="ui small header"> {{sToExitClass.name}}  {{ klass.name}}</p>
        <p class="text-muted">
          <span class="text-danger"> {{klass.name}} </span>, <br>
           {{klass.name}}  <span class="text-danger"></span> 
        </p>
        <p>
          <button class="ui tiny blue button right floated" @click="postExitClass($event)"></button>
          <button class="ui tiny button right floated" @click="sToExitClass=null"></button>
        </p>
      </div>
    </page-modal>
    <page-modal
      v-if="sToTransfer && klass"
      @close="sToTransfer=null"
      class="modal-padded modal-small">
      <div slot="content">
        <p class="ui small header">{{sToTransfer.name}} </p>
        <p class="text-muted">
          {{klass.name}}ID<br>
          
        </p>
        <div class="ui small form">
          <div class="field required">
            <label> {{klass.name}} </label>
            <div class="ui search selection dropdown">
              <i class="dropdown icon"></i>
              <div class="text"></div>
              <div class="menu">
                <template v-for="c of classesOfSameGrade">
                  <div class="item" v-if="c.id!=klass.id" @click="transferToClassId=c.id"> {{c.name}} </div>
                </template>
              </div>
            </div>
          </div>
          <button class="ui tiny blue button" @click="postTransferClass($event)"></button>
          <button class="ui tiny button" @click="sToTransfer=null"></button>
        </div>
      </div>
    </page-modal>

    <page-modal
      v-if="sToBan"
      @close="sToBan=null"
      class="modal-padded modal-small">
      <div slot="content">
        <p class="ui small header"> {{sToBan.name}}</p>
        <p class="text-muted">
          ?
        </p>
        <p>
          <button class="ui tiny red button right floated" @click="postBanStudent($event)"></button>
          <button class="ui tiny button right floated" @click="sToBan=null"></button>
        </p>
      </div>
    </page-modal>
    <page-modal
      v-if="sToRecover"
      @close="sToRecover=null"
      class="modal-padded modal-small">
      <div slot="content">
        <p class="ui small header"> {{sToRecover.name}}</p>
        <p class="text-muted">
          ?
        </p>
        <p>
          <button class="ui tiny blue button right floated" @click="postRecoverStudent($event)"></button>
          <button class="ui tiny button right floated" @click="sToRecover=null"></button>
        </p>
      </div>
    </page-modal>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import axios from 'axios'
import PageModal from 'components/page-modal'
import Pagination from 'components/pagination'
import Loader from 'components/loader'
import { CLASS_STATUS_NORMAL, GRADES, ACCOUNT_STATUS_NORMAL} from '@/assets/js/consts/common'

const qs = require('qs')

export default {
  name: 'student-paging-list',
  props: {
    klass: { type: Object, default: null }, // 
    students: { type: Array, required: true },
    loading: { type: Boolean, default: false },
    totalPage: { type: Number, default: 0 },
    curPage: { type: Number, default: 0 },
    showClasses: { type: Boolean, default: false },
  },
  data() {
    return {
      GRADES,
      sToResetPassword: null,

      sToEdit: null,
      snameToEdit: '',
      snumberToEdit: '',

      sToExitClass: null,
      sToTransfer: null,
      transferToClassId: 0,
      classesOfSameGrade: null,

      sToBan: null,
      sToRecover: null,

    }
  },
  components: { Pagination, Loader, PageModal },
  mounted() {
    if (this.teacher.isManager && this.klass) {
      this.fetchClasses()
    }
  },
  computed: {
    ...mapState(['teacher']),
  },
  methods: {
    isStatusNormal: student => student.status === ACCOUNT_STATUS_NORMAL,
    openClassInNewTab(klass) {
      window.open(this.$router.resolve({ name: 'org-class-detail', params: { id: klass.id } }).href, '_blank')
    },
    openEditStudentModal(student) {
      this.snameToEdit = student.name
      this.snumberToEdit = student.number
      this.sToEdit = student
    },
    openTransferModal(student) {
      if (!this.classesOfSameGrade) {
        this.$toasted.info('1...')
        return
      }
      this.transferToClassId = 0
      this.sToTransfer = student
      this.$nextTick(() => { $(this.$el.querySelector('.ui.dropdown')).dropdown() })
    },
    fetchClasses() {
      const params = {
        gradeId: this.klass.gradeId,
        status: CLASS_STATUS_NORMAL,
        fields: ['id', 'name', 'status'],
      }

      axios.get('/pad/j/management/classes/all', { params }).then((response) => {
        if (response.data.status === 0) {
          this.classesOfSameGrade = response.data.classes
        } else if (response.data.status === 1) {
          this.$toasted.error(response.data.detail)
        }
      })
    },
    postTransferClass(event) {
      if (!this.transferToClassId) {
        this.$toasted.error('')
        return
      }

      const url = `/pad/j/management/students/${this.sToTransfer.id}/transfer/`
      const params = { oldClassId: this.klass.id, newClassId: this.transferToClassId }
      $(event.target).addClass('loading')
      axios.post(url, qs.stringify(params)).then((resp) => {
        if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail)
        } else if (resp.data.status === 0) {
          this.$toasted.success('')
          this.$emit('studentTransfered', this.sToTransfer)
          this.sToTransfer = null
        }
        $(event.target).removeClass('loading')
      })
    },
    postExitClass(event) {
      const url = `/pad/j/management/classes/${this.klass.id}/students/${this.sToExitClass.id}/exit/`
      $(event.target).addClass('loading')

      axios.post(url).then((resp) => {
        if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail)
        } else if (resp.data.status === 0) {
          this.$toasted.success('')
          this.$emit('studentExited', this.sToExitClass)
          this.sToExitClass = null
        }
        $(event.target).removeClass('loading')
      })
    },
    postRecoverStudent(event) {
      const url = `/pad/j/management/students/${this.sToRecover.id}/recover/`
      $(event.target).addClass('loading')
      axios.post(url).then((resp) => {
        if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail)
        } else if (resp.data.status === 0) {
          this.$toasted.success('')
          this.sToRecover = null
          this.$emit('studentUpdated', resp.data.student)
        }
        $(event.target).removeClass('loading')
      })
    },
    postBanStudent(event) {
      const url = `/pad/j/management/students/${this.sToBan.id}/ban/`
      $(event.target).addClass('loading')
      axios.post(url).then((resp) => {
        if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail)
        } else if (resp.data.status === 0) {
          this.$toasted.success('')
          this.sToBan = null
          this.$emit('studentUpdated', resp.data.student)
        }
        $(event.target).removeClass('loading')
      })
    },
    postEditStudent(event) {
      const url = `/pad/j/management/students/${this.sToEdit.id}/edit/`
      if (!(this.snameToEdit.length && this.snameToEdit.length <= 20)) {
        this.$toasted.error('20')
        return
      }
      if (this.snumberToEdit.length > 20) {
        this.$toasted.error('20')
      }

      const params = { name: this.snameToEdit, number: this.snumberToEdit }
      $(event.target).addClass('loading')

      axios.post(url, qs.stringify(params)).then((resp) => {
        if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail)
        } else if (resp.data.status === 0) {
          this.$toasted.success('')
          this.sToEdit = null
          this.$emit('studentUpdated', resp.data.student)
        }
        $(event.target).removeClass('loading')
      })
    },
    postResetPassword(event) {
      if (!this.klass) {
        this.$toasted.info('')
      }
      const url = `/pad/j/management/classes/${this.klass.id}/students/${this.sToResetPassword.id}/reset_password/`
      $(event.target).addClass('loading')

      axios.post(url).then((resp) => {
        if (resp.data.status === 1) {
          this.$toasted.error(resp.data.detail)
        } else if (resp.data.status === 0) {
          this.$toasted.success('')
          this.sToResetPassword = null
        }
        $(event.target).removeClass('loading')
      })
    },

  },
}
</script>

<style lang="less" scoped>
</style>




